.contact-wrapper{
    display: grid;
    gap: 40px;
    grid-template-columns: repeat(3,1fr);
}
.c-section-heading{
    font-size: 30px;
    text-transform: capitalize;
    margin: 20px 0px;
}
.contact-item{
    display: flex;
    padding: 10px;
}
.c-icon{
   height: 50px;
   width: 50px;
   border-radius: 25px;
   font-size: 20px;
   background-color: rgba(102, 141, 147, 0.45);
   display: flex;
   justify-content: center;
   align-items: center;
   color: var(--green);
   overflow: hidden;
   margin-right: 15px;
}
.upercase{
    text-transform: capitalize;
}
.contact-right>p{
    
    margin: 5px 0;
}
@media (max-width:1040px) {
    .contact-wrapper{
        grid-template-columns: repeat(2,1fr);
    } 
}
@media (max-width:550px) {
    .contact-wrapper{
           grid-template-columns: repeat(1,1fr);
    }
 
}