.about-container{
    margin: auto;
    /* border: 1px solid red; */
}.about-content-container{
    display: grid;
    /* justify-content: center; */
    align-items: center;
    grid-template-columns: 50% 50%;
    gap: 40px;
}
.a-left>img{
    width: 100%;
    border: 6px solid rgb(64, 240, 187);
    border-radius: 5px;
}
.a-name{
    font-size: 1.5rem;
    font-weight: 500;
    margin: 10px 0px;
}
.details{
    display: flex;
}
.flex{
    display: flex;
    /* justify-content: center; */
    align-items: center;
    color: white;
    /* border: 1px solid red; */

}
.mt{
    margin-top: 20px;
}
.details-right-item{
    display: flex;
    color: white;
    /* justify-content: center; */
    align-items: center;
    text-transform: capitalize;
    width: 130px;
    margin: 5px 0px;
    /* margin-left: -10px; */
}
.dot{
    font-size: 25px;
    color: var(--green);
    width: fit-content;
}
@media (max-width:550px) {
    .about-content-container{
        padding: 10px;
        grid-template-columns: 100%;
    } 
}