.home-container{
    /* border: 1px solid red; */
    /* margin-top: 40px; */
    position: relative;
}
.empty{
    height: 50px;
}
.my-img{
    width: fit-content;
    overflow: hidden;
    border-radius: 50%;
    border: 9px solid rgba(20, 123, 137, 0.626);
    /* border: 9px solid var(--green); */
    margin: auto;
    /* margin-top: 50px; */
}.home-intro{
    color: white;
    margin: 10px 0;
    font-size: 2.5rem;
}
.my-name{
    font-size: 2.4rem;
}
.home-intro-desc{
    letter-spacing: 1px;
    width: 80%;
    align-items: center;
   margin: auto;
   margin-top: 30px;
   margin-bottom: 30px;
   line-height: 1.7rem;
color: white;
}

.home-social .github,.home-social .linkedin{
    font-size: 1.6rem;
    padding: 7px;
    color: var(--green);
    margin: 10px 5px;
    /* color: rgb(0,217,255); */
    /* background-color:var(--bg) ; */
    /* box-shadow: rgba(20, 123, 137, 0.626) 0 0 1px 1px ; */
}
@media screen and (max-width:1040px) {
    .home-container{
        /* border: 1px solid red; */
        width: 100%;
        /* border: 1px solid red; */
    }
    .home-intro-desc{
        width: 90%;
    }
}