.card-container{
    /* border: 1px solid red; */
    width: 100%;
    padding: 1rem;
    box-shadow: rgba(17, 204, 195, 0.24) 0px 3px 8px;
    /* border: 0.5px solid var(--green); */
    border: 0.5px solid rgb(135, 132, 132);

    
}
.card-container *{
    margin: 15px 0px;
}
.card-img>img{
    width: 100%;
    display: block;
    margin: auto;
    opacity: 0.7;
}
.card-title{
    font-size: 1.5rem;
    text-align: center;
    margin: 20px 0;
    text-transform: capitalize;
    color: white;
}
.project-desc{
    letter-spacing: 1px;
}
.tech-stack{
    color: white;
}
.head{
    
    margin-right: 10px;
}
.item{
    text-transform: uppercase;
    margin: 0 5px;
    font-size: 0.8rem;
}
.btn-container{
    margin: 10px 0;
    display: flex;
    justify-content: space-between;
}
@media screen and(max-width:1040px){
    .card-container{
        width: 65%;
    }
}