.project-container{
    width: 100%;
}
.project-card-container{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 30px;
}
@media screen and (max-width:1040px) {
    .project-card-container{
        grid-template-columns:100%;
        margin: auto;
    }
}