.header{
    background: rgb(15,23,44);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 6rem;
    position: sticky;
    top: 0px;
    width: 100%;
    height: 70px;
    z-index: 10;
    border-bottom: 0.5px solid rgba(167, 220, 252, 0.5);
}
.header>ul{
    list-style: none;
}
.heading{
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 35px;
}
.header-bg{
    background-color: rgba(0, 0, 0, 0.5);
    transition: 0.5s;

}
.hamburger{
    display: none;
}
.nav-menu{
    display: flex;

}
.nav-menu li{
    padding: 0 1rem;
   
}
.nav-menu li>span{
    font-size: 1rem;
    margin-right: 5px;
}
.nav-menu li a{
    /* text-transform: uppercase; */
    color: white;
    font-size: 1rem;
    cursor: pointer;
    margin-right: 30px;
    font-weight: 500;
}
.nav-menu li a:hover{
    text-decoration: underline;
}
@media screen and (max-width:1040px){
    .header{
        padding: 0px 10px;
        /* filter: blur(8px); */

    }
    .nav-menu{
        /* border: solid red; */
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 50%;
        height: 100vh;
        background:rgb(10, 35, 29);
        /* background:rgba(149, 114, 114, 0.2); */
        position: absolute;
        top: 70px;
        right: -100%;
        transition: 0.7s;
        z-index: 553;
       background-color: rgb(3, 72, 72);
       /* opacity: 0.9; */
       /* filter: blur(3px); */
    }
    .nav-menu.active{
        right:  0;
    }
    .nav-menu li{
        padding: 1rem 0;
    }
    .nav-menu li a{
        font-style: 2rem;
    }
    .hamburger{
        display: inline;
        cursor: pointer;
    }
}