@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600&family=Poppins:wght@300;400&family=Roboto:wght@100;300;400;500&display=swap');
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    /* font-family: 'Poppins', sans-serif; */
    font-family: 'Roboto', sans-serif;
}
:root {
    --green: rgb(114,226,174);
    --bg:rgb(117, 125, 130);
}
body{
    /* background: rgba(11,18,35,0.9); */
    background: rgb(15,23,44);
}
a{
    text-decoration: none;
    color: black;

}
/* ul{
    list-style: none;
} */
.green{
    color: var(--green);
}
.box{
    /* background-color: var(--bg); */
    border-radius: 4px;
    box-shadow: rgba(229, 227, 227, 0.15) 0px 0px 1px 1px;
}
.white{
    color: white;
}
.center{
    text-align: center;
    margin: 10px 0
}
.text{
    opacity: 0.9;
}
.btn{
    /* font-size: 2rem; */
    color: black;
    border: none;
    text-decoration: none;
    cursor: pointer;
    letter-spacing: 1px;
    padding: 0.6rem 1rem;
    border-radius: 4px;
    background-color: var(--green);
    text-transform: uppercase;
}
.title{
    text-transform: uppercase;
    color: white;
    font-size: 1.9rem;
    letter-spacing: 2px;
    text-align: center;
    margin: 50px 0;
}
.layout{
    /* border: 1px solid red; */
    width: 85%;
    /* height: 100vh; */
    margin: auto;
    padding: 50px 0;
    /* margin-top: 40px; */
    /* margin-bottom: 40px; */
    /* border: 1px solid red; */
}
@media screen and (max-width:1040px){
    .layout{
        width: 95%;
    }
}